.header__content {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin: 0 0 40px;
  padding-top: 45px;
  min-height: 74px;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

@media (max-width: 768px) {
  .header__content {
    margin: 28px 0 42px;
    padding-left: 27px;
    min-height: 56px;
  }
}
